import { LOGIN } from "./action";

const initState = {
  isLoggedIn: false,
  errMsg: ""
};

export default (state = initState, action) => {
  switch (action.type) {
    case LOGIN:
      const { isLoggedIn, errMsg } = action.payload;
      state = isLoggedIn
        ? {
            ...state,
            isLoggedIn: isLoggedIn,
            errMsg: ""
          }
        : {
            ...state,
            isLoggedIn: isLoggedIn,
            errMsg: errMsg
          };
      break;
    default:
      break;
  }
  return state;
};
