import React from 'react';
const clickOutside = (WrappedComponent) => {
    return class extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                isOpen: false,
                v: 1
            };
            this.node = React.createRef();
            this.isCanceled = false;
        }

        componentDidMount() {
            const {isOpen} = this.state;
            document.addEventListener('mousedown', this.handlerClick(isOpen), false);
        }

        componentWillUnmount() {
            document.removeEventListener('mousedown', this.handlerClick, false);
            this.isCanceled = true;
        }

        handlerClick = (isOpen) => (event) => {           
            if (!this.isCanceled) {
                if (this.node.contains(event.target)) {
                    setTimeout(() => {
                        this.toggle();
                    }, 200);
                } else if(isOpen){
                    this.setState({
                        isOpen: false,
                        v: this.state.v + 1
                    });
                }
            }
        }

        toggle = () => {
            if (!this.isCanceled) {
                this.setState({
                    isOpen: !this.state.isOpen,
                    v: this.state.v + 1
                });
            }
        }

        shouldComponentUpdate(nextProps, nextState) {
            return (this.state.v !== nextState.v);
        }

        render() {
            return <WrappedComponent nodeRef={ el => this.node = el } isOpen={ this.state.isOpen }></WrappedComponent>
        }
    }
}

export default clickOutside;