import {
  UPLOAD_FINISH,
  UPDATE_PROGRESS,
  DELETE_IMAGE,
  CLEAN_UP
} from "./action";

const initState = {
  finished: false,
  uploadName: "",
  progress: 0,
  fileUrls: [],
  v: 1
};

export default (state = initState, action) => {
  switch (action.type) {
    case UPLOAD_FINISH:
      state = {
        ...state,
        finished: action.payload.finished,
        uploadName: action.payload.uploadName,
        fileUrls: action.payload.fileUrls,
        v: state.v + 1
      };
      break;
    case UPDATE_PROGRESS:
      state = {
        ...state,
        finished: action.payload.finished,
        uploadName: action.payload.uploadName,
        progress: action.payload.progress,
        v: state.v + 1
      };
      break;
    case DELETE_IMAGE:
      const { index } = action.payload;
      let { fileUrls } = state;
      // eslint-disable-next-line no-unused-expressions
      fileUrls && fileUrls.length > 0 ? fileUrls.splice(index, 1) : fileUrls;
      state = {
        ...state,
        fileUrls: [...fileUrls],
        v: state.v + 1
      };
      break;
    case CLEAN_UP:
      state = {
        ...initState,
        v: state.v + 1
      };
    // eslint-disable-next-line no-fallthrough
    default:
      break;
  }
  return state;
};
