export const UPDATE_TABLE_BODY = "UPDATE_TABLE_BODY";

/**
 *
 * @param {*} tableName Table name to update which table
 * @param {*} data Data of body to rerender
 */
export const updateBody = (tableName, data) => {
  return dispatch => {
    dispatch({
      type: UPDATE_TABLE_BODY,
      payload: {
        data: data,
        tableName: tableName
      }
    });
  };
};
