import React from "react";

// Import styles
import "./style.css";

// Import components
import DropdownMenuItem from "./../DropdownMenuItem";

class DropdownMenu extends React.Component {
  render() {
    const { listMenu, menuStyles, isOpen } = this.props;
    const generateMenu = listMenu.map((res, index) => {
      return (
        <DropdownMenuItem
          key={index}
          menuItem={res}
          handlerClick={res.handlerClick}
        />
      );
    });
    const className = isOpen ? "dropdown-menu open" : "dropdown-menu close";
    return (
      <div className={className} style={menuStyles}>
        {generateMenu}
      </div>
    );
  }
}

export default DropdownMenu;
