import { UPDATE_TABLE_BODY } from "./action";

const initState = {
  tableName: "",
  data: [],
  count: 0,
  v: 0
};

export default (state = initState, action) => {
  switch (action.type) {
    case UPDATE_TABLE_BODY:
      state = {
        ...state,
        data: action.payload.data.rows,
        count: action.payload.data.count,
        v: state.v + 1
      };
      break;
    default:
      break;
  }
  return state;
};
