import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { compose } from "recompose";

// Import images
import logo from "./../../images/logo-trends-featured.png";

// Import styles
import "./style.css";

// Import validator
import { required, email } from "../../validator/validator-input";

// Import render Input
import { renderInput } from "../../renderfields/dask";

// Import actions
import { login, checkAuth } from "../../services/account/action";

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      remember: false,
      rememberClass: "checkbox lbl-remember"
    };
    this.remember = this.remember.bind(this);
    this.submitLogin = this.submitLogin.bind(this);
  }

  remember() {
    this.setState({
      remember: !this.state.remember,
      rememberClass: !this.state.remember
        ? "checkbox active lbl-remember"
        : "checkbox lbl-remember"
    });
  }  

  submitLogin(values) {
    const { history } = this.props;
    login({username:values.email, password:values.password})(this.props.dispatch, history, this.state.remember);
  }

  componentDidMount() {
    if (checkAuth()) {
      const { history } = this.props;
      history.push("/orders");
    }
  }

  render() {
    const { pristine, handleSubmit, submitting, invalid, errMsg } = this.props;
    return (
      <form className="login-page" onSubmit={handleSubmit(this.submitLogin)}>
        <div className="login-box">
          <div className="logo-box">
            <img className="logo" src={logo} alt="" />
          </div>
          <div className="login-form">
            <h3 className="font-green">Sign In</h3>
            <div className="summary-error-msg">
              <span className="message error">{errMsg}</span>
            </div>
            <div className="form-group">
              <Field
                type="text"
                className="form-control"
                name="email"
                component={renderInput}
                label="Enter your email"
                validate={[required, email]}
              />
            </div>
            <div className="form-group">
              <Field
                type="password"
                className="form-control"
                name="password"
                component={renderInput}
                label="Enter your password"
                validate={[required]}
              />
            </div>
            <div className="form-actions">
              <button
                onClick={this.login}
                disabled={pristine || submitting || invalid}
                className="btn btn-green"
              >
                {" "}
                Login
              </button>
              <label htmlFor="remember" className={this.state.rememberClass}>
                <input
                  type="checkbox"
                  onChange={this.remember}
                  className="remember"
                  id="remember"
                />{" "}
                Remember
                <span />
              </label>              
            </div>
          </div>
        </div>
      </form>
    );
  }
}

const mapStateToProps = state => {
  const { errMsg } = state.account;
  return {
    errMsg: errMsg
  };
};

const enhance = compose(
  reduxForm({
    form: "loginForm"
  }),
  connect(mapStateToProps)
);

export default enhance(Login);
