import { RENDER_MENU } from "./action";

const initState = {
  render: false
};

export default (state = initState, action) => {
  switch (action.type) {
    case RENDER_MENU:
      if (state.render !== action.payload.render) {
        state = {
          ...state,
          render: action.payload.render
        };
      }
      break;
    default:
      break;
  }
  return state;
};
