export const RENDER_MENU = "RENDER_MENU";

export function updateMenu() {
  return function(dispatch) {
    dispatch({
      type: RENDER_MENU,
      payload: {
        render: true
      }
    });
  };
}

export function cleanupMenu() {
  return function(dispatch) {
    dispatch({
      type: RENDER_MENU,
      payload: {
        render: false
      }
    });
  };
}
