import { UPDATE_STATE } from "./action";
import { buttonState } from "../../const/enum";

/**
 * loading
 * disabled
 * success
 * error
 * nothing
 */
const initState = {
  buttonState: buttonState.NOTHING,
  actionName: "",
  v: 1
};

export default (state = initState, action) => {
  switch (action.type) {
    case UPDATE_STATE:
      state = {
        ...state,
        buttonState: action.payload.buttonState,
        actionName: action.payload.actionName,
        v: state.v + 1
      };
      break;
    default:
      break;
  }
  return state;
};
