import accountApi from "./api";
import cookieManager from "../cookieManager";
import {axiosAuthRequestConfig} from "../../services/base.http";

export const LOGIN = "LOGIN";

/**
 *
 * @param {*} data Email and password
 * The login function will authenticate the, will save the token into cookie and update base http config authorization
 * Finnally dispatch to redirect the application to dashboard
 */
export function login(data) {
  // return function (dispatch, history, isRemember) {   debugger   const userName
  // = 'admin@gmail.com';   const password = 'admin';   if (data.email ===
  // userName && data.password === password) {     const exDays = isRemember
  // ? 30       : 7;     cookieManager.setToken('admin', exDays);
  // cookieManager.setUserInfo({       userName: 'admin'     }, exDays);
  // axiosAuthRequestConfig.headers.Authorization = "Bearer " +
  // cookieManager.getToken();     dispatch({       type: LOGIN,       payload: {
  //        isLoggedIn: true       }     });     history.push("/dashboard");   }
  // else {   } }
  return function (dispatch, history, isRemember) {
    accountApi
      .login(data)
      .then(res => {
        debugger
        const exDays = isRemember
          ? 30
          : 7;
        cookieManager.setToken(res.data.success, exDays);
        cookieManager.setUserInfo(res.data.success, exDays);
        axiosAuthRequestConfig.headers.Authorization = "Bearer " + cookieManager.getToken();
        dispatch({
          type: LOGIN,
          payload: {
            isLoggedIn: true
          }
        });
        history.push("/orders");
      })
      .catch(err => {
        const {response} = err;
        if (response.status === 401) {
          dispatch({
            type: LOGIN,
            payload: {
              isLoggedIn: false,
              errMsg: "Email or password is incorrect"
            }
          });
        }
      });
  };
}

/**
 * Check the token whether is it existing in local storage or not
 */
export function checkAuth() {
  const token = cookieManager.getToken();
  return token
    ? true
    : false;
}

/**
 *
 * @param {*} role The role of user
 * Checking role base information from cookie
 */
export function checkRole(role) {
  return true;
}

/**
 * Clean the cookie information and send dispatch to redirect the application to logout
 */
export function logout() {
  cookieManager.removeToken();
  cookieManager.removeUserInfo();
  return function (dispatch) {
    dispatch({
      type: LOGIN,
      payload: {
        isLoggedIn: false
      }
    });
  };
}
