import firebase from "../../firebase-config";
import { generateNewFileName } from "../common";

let storeageRef = firebase.storage().ref();

export const UPLOAD_FINISH = "UPLOAD_FINISH";
export const UPDATE_PROGRESS = "UPDATE_PROGRESS";
export const DELETE_IMAGE = "DELETE_IMAGE";
export const CLEAN_UP = "CLEAN_UP";

export function uploadFile(file, uploadName) {
  const newFileName = generateNewFileName(file.name);

  return function(dispatch) {
    let imageRef = storeageRef.child(uploadName + "/" + newFileName);
    const uploadTask = imageRef.put(file);
    uploadTask
      .then(snapShot => {
        imageRef
          .getDownloadURL()
          .then(url => {
            dispatch({
              type: UPLOAD_FINISH,
              payload: {
                finished: true,
                uploadName: uploadName,
                fileUrls: [url]
              }
            });
          })
          .catch(err => {
            console.log("Can not load firl url");
          });
      })
      .catch(err => {
        console.log("Can not upload file");
      });
    uploadTask.on(
      "state_changed",
      snapshot => {
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        const progressData =
          progress < 100
            ? {
                finished: false,
                progress: progress
              }
            : {
                finished: true,
                progress: 0
              };
        dispatch({
          type: UPDATE_PROGRESS,
          payload: {
            finished: progressData.finished,
            progress: progressData.progress,
            uploadName: uploadName
          }
        });
      },
      err => {
        console.log("Can not upload file");
      }
    );
  };
}

export function uploadFiles(files, uploadName) {
  let tasks = [];
  let downloadUrlTasks = [];
  for (let file of files) {
    const newFileName = generateNewFileName(file.name);
    let imageRef = storeageRef.child(uploadName + "/" + newFileName);
    tasks.push(imageRef.put(file));
    downloadUrlTasks.push(imageRef.getDownloadURL());
  }
  return function(dispatch) {
    Promise.all(tasks)
      .then(res => {
        console.log(res);
        Promise.all(downloadUrlTasks)
          .then(downloadRes => {
            dispatch({
              type: UPLOAD_FINISH,
              payload: {
                finished: true,
                uploadName: uploadName,
                fileUrls: downloadRes
              }
            });
          })
          .catch(err => {
            console.log("Can not download file url");
          });
      })
      .catch(err => {
        console.log("Can not upload file");
      });
  };
}

export function deleteFile(index, uploadName) {
  return function(dispatch) {
    dispatch({
      type: DELETE_IMAGE,
      payload: {
        uploadName: uploadName,
        index: index
      }
    });
  };
}

export function cleanupUpload() {
  return function(dispatch) {
    dispatch({
      type: CLEAN_UP,
      payload: {}
    });
  };
}
