import React from "react";
import {ToastContainer, ToastStore} from "react-toasts";
import {connect} from "react-redux";

// Import components
import Header from "../components/Header";
import Body from "../components/Body";

// Import actions
import { logout } from "../services/account/action";

class MainLayout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {  
    debugger  
    // document.removeEventListener('onbeforeunload', this.closeWindow, false);   
    // document.addEventListener('onbeforeunload', this.closeWindow(), false); 
    var _this = this;     
    window.onbeforeunload = function(event){
      if(event.type === 'beforeunload')
        _this.closeWindow(event);
    };
  }

  closeWindow(e) {
    debugger
    logout();
  }

  render() {
    return (
      <div>
        <Header/>
        <Body>{this.props.children}</Body>
        <ToastContainer
          store={ToastStore}
          position={ToastContainer.POSITION.TOP_RIGHT}
          lightBackground/>
      </div>
    );
  }
}

const mapStateToProps = state => {
  if (state.toast) {}
  return {};
};

export default connect(mapStateToProps)(MainLayout);
