import { baseHttp } from "../base.http";
import apiUrl from "../../const/apiUrl";

const accountApi = {
  login: function(data) {
    let requestConfig = {
      headers: {
        Authorization: "Basic " + btoa(data.email + ":" + data.password)
      }
    };
    return baseHttp.post(apiUrl.LOGIN, data, requestConfig);
  }
};

export default accountApi;
