import categoryApi from "./api";

export const DELETE_CATEGORY = "DELETE_CATEGORY";

export function getAllCategories() {
  return categoryApi.getAll().then(res => res.data);
}

export function getById(id) {
  return categoryApi.getById(id).then(res => res.data);
}

export function createCategory(data) {
  return categoryApi.create(data);
}

export function updateCategory(data) {
  return categoryApi.update(data);
}

export function updateCategoryTree(data) {
  return categoryApi.updateTree(data);
}

export function deleteCategory(id) {
  return function(dispatch) {
    categoryApi.delete(id).then(res => {
      dispatch({
        type: DELETE_CATEGORY,
        payload: {
          id: id
        }
      });
    });
  };
}
