import React from "react";

// Import hoc
import clickOutside from "../../../../hoc/clickOutside";

// Import styles
import "./style.css";

// Import actions
import { logout } from "../../../../services/account/action";

// Import context
import { MatchPropsContext } from "../../../../context";

// Import components
import DropdownMenu from "../../../DropdownMenu";

class UserBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      listMenu: []
    };
    const { history } = this.props.matchProps;
    // this.state.listMenu.push({
    //   icon: "far fa-user",
    //   name: "My Profile"
    // });
    this.state.listMenu.push({
      icon: "fas fa-lock",
      name: "Logout",
      handlerClick: () => {
        logout();
        history.push("/login");
      }
    });
  }

  shouldComponentUpdate(nextProps) {
    return this.props.isOpen !== nextProps.isOpen;
  }

  render() {
    console.log("bind");
    return (
      <div
        ref={this.props.nodeRef}
        className="user-bar display-flex  align-items-center"
      >
        <img src="" />
        <span>Admin</span>
        <i className="fas fa-angle-down" />
        <DropdownMenu
          listMenu={this.state.listMenu}
          isOpen={this.props.isOpen}
          menuStyles={styles.menuStyles}
        />
      </div>
    );
  }
}

const UserBarWrrap = props => (
  <MatchPropsContext.Consumer>
    {matchProps => <UserBar {...props} matchProps={matchProps} />}
  </MatchPropsContext.Consumer>
);

const styles = {
  menuStyles: {
    top: "100%",
    right: "-8px",
    width: "160px"
  }
};

export default clickOutside(UserBarWrrap);
