import React from "react";

// Import styles
import "./style.css";

class IconBar extends React.Component {
  shouldComponentUpdate() {
    return false;
  }

  render() {
    return (
      <div className="icon-bar display-flex align-items-center justify-content-center">
        <span className="bag">{this.props.number}</span>
        <i className={this.props.icon} />
      </div>
    );
  }
}

export default IconBar;
