import { DELETE_CATEGORY } from "./action";

const initState = {
  id: "",
  v: 1
};

export default (state = initState, action) => {
  const { id } = action.payload ? action.payload : "";
  switch (action.type) {
    case DELETE_CATEGORY:
      if (id !== state.id) {
        state = {
          ...state,
          id: id,
          v: state.v + 1
        };
      }
      break;
  }
  return state;
};
