import React from "react";
import ReactDOM from "react-dom";
import registerServiceWorker from "./registerServiceWorker";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store";
import Loadable from "react-loadable";

// Import components
import Login from "./scenes/Login";
import MainLayout from "./layouts/MainLayout";

// Import styles
import "./styles/app.css";
import "./styles/normalize.css";

// Import services
import { checkAuth, checkRole } from "./services/account/action";

// Import context
import { MatchPropsContext } from "./context";

const MainLayoutRoute = ({ component: Component, ...rest }) => {
  // If the user is authenticated. Render normal component. Redirect to login if not
  if (checkAuth()) {
    // If the user does not have role. We will redirect them to dashboard
    if (checkRole()) {
      return (
        <Route
          {...rest}
          render={matchProps => (
            <MatchPropsContext.Provider value={matchProps}>
              <MainLayout>
                <Component {...matchProps} />
              </MainLayout>
            </MatchPropsContext.Provider>
          )}
        />
      );
    } else {
      return (
        <Route {...rest} render={matchProps => <Redirect to="/login" />} />
      );
    }
  } else {
    return <Route {...rest} render={matchProps => <Redirect to="/login" />} />;
  }
};

const Dashboard = Loadable({
  loader: () => import("./scenes/Dashboard"),
  loading: function() {
    return <div>Loading...</div>;
  }
});

/*
* Make the dashboard component preload because after login user is always redirected to dashbpard page
* It means when the user access the login page. The Dashboard will be loaded concurrency
*/
Dashboard.preload();

ReactDOM.render(
  <BrowserRouter>
    <Provider store={store}>
      <Switch>
        <Route exact path="/login" component={Login} />
        <Route exact path="/" component={Login} />
        <MainLayoutRoute exact path="/orders" component={Dashboard} />       
        <MainLayoutRoute path="*" component={Dashboard} />
      </Switch>
    </Provider>
  </BrowserRouter>,
  document.getElementById("root")
);
registerServiceWorker();
