import { THROW_MSG } from "./action";

const initState = {
  msg: "",
  type: ""
};

export default (state = initState, action) => {
  switch (action.type) {
    case THROW_MSG:
      state = {
        ...state,
        msg: action.payload.msg,
        type: action.payload.type
      };
      break;
    default:
      break;
  }
  return state;
};
